body {
  background-color: #fff;
}

.navbar-nav {
  align-items: center;
  display: flex;
}

.navbar-nav .nav-link {
  color: #6c757d;
  margin-right: 3rem;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 24px;
}

.navbar-brand {
  margin-right: 3rem;
}

.register-container {
  justify-content: center;
  align-items: center;
  height: calc(100vh - 150px);
  display: flex;
}

.register-section {
  background-color: #fff;
  border: 1px solid #696969;
  border-radius: 5px;
  width: 400px;
  margin: 50px auto;
  padding: 20px;
}

.register-section form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.register-section img {
  margin-bottom: 20px;
}

.register-section button, .register-section p {
  margin-top: 20px;
}

.register-section {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 0 10px #0000001a;
}

.register-input {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 30px;
  width: 100%;
  max-width: 400px;
  margin: 10px 0;
  padding: 10px;
}

.register-textarea {
  resize: none;
  border: 1px solid #ccc;
  border-radius: 0;
  width: 100%;
  max-width: 400px;
  height: 100px;
  margin: 10px 0;
  padding: 15px;
}

.register-button {
  color: #fff;
  background-color: #6495ed;
  border: none;
  border-radius: 30px;
  width: 100%;
  max-width: 200px;
  margin: 10px 0;
  padding: 10px 20px;
  font-family: Inter, sans-serif;
  font-weight: bold;
}

.register-button:hover {
  cursor: pointer;
  background-color: #4169e1;
}

.create-account {
  color: #6495ed;
  font-weight: bold;
  text-decoration: none;
}

.create-account:hover {
  text-decoration: underline;
}
